@import "../../styles/variables";

.register-selection-page{
    display:flex;
    background-image: url(https://www.cypressgreen.in/blog/wp-content/uploads/2021/03/food-1536x1024.jpg);
    background-size: cover;
    background-position: center;

    height:100vh;


    .register-container{
        margin:auto;
        width:calc(60% - 10px);
        background:$color_cream;
        border-radius:10px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;

        .register-title{
            margin-top:30px;
            color:$color_brown;
        }

        .register-option-container{
            width:100%;
            padding: 40px 25px;
            display:flex;
            justify-content: space-evenly;
            align-items: center;
        }

        .default-button{
             margin-bottom:30px;
        }
    }
}