@import "../../styles/variables";

.banner {
    z-index: 0;
    background: url(https://iili.io/7XpyPf.md.png);
    background-size: cover;
    background-position: center;
    width: calc(90% - 40px);
    height: 500px;
    padding: 20px;
    margin: 20px auto;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    border-radius: 10px;
    box-shadow: 0px 0px 10px $color_brown;
    .banner-img {
        z-index: 1;
        background-color: rgba($color_brown, 0.4);
        width: 100%;
        height: 100%;
        position: absolute;
        border-radius: 10px;
    }
    .banner-text {
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        color: $color_cream;
        border-radius: 5px;
        h1 {
            font-size: 3em;
            font-weight: bold;
            margin: 0;
        }
        p {
            font-size: 1.5em;
            margin: 0;
        }
        a {
            width: 200px;
            height:fit-content;
            margin: 10px 0 0 0;
            font-size: 1.5em;
            padding: 10px;
            background: $color_green;
            border-radius: 5px;
            color: $color_cream;
            text-decoration: none;
            transition: all 0.5s ease;
            &:hover {
               background: rgba($color_green, 0.85);
            }
        }
    }
}
